.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: 50%;
  }
  
  @for $index from 1 through 5 {
    &--#{$index} .stars__star:nth-child(-n + #{$index}) {
      background-image: url(../images/star-active.svg);
    }
  }
}
